import axios from "axios";
import { ElMessage } from "element-plus";
axios.defaults.timeout = 10000; // Ë∂ÖÊó∂Êó∂Èó¥
// https://apifoxmock.com/m2/3898618-3533065-default/190434496?apifoxApiId=190434496
axios.defaults.baseURL = "https://apifoxmock.com/m1/3898618-3533065-default"; //"http://127.0.0.1:8080";
// Êï∞ÊçÆÊ†ºÂºèËΩ¨Êç¢
axios.defaults.transformRequest = function (data) {
  data = JSON.stringify(data);
  return data;
};

// Ë∑ØÁî±ËØ∑Ê±ÇÊã¶Êà™
axios.interceptors.request.use(
  (config) => {
    config.headers["Content-Type"] = "application/json;charset=UTF-8";
    try {
      // ËÆæÁΩÆÂâçÁ´Øtoken
      const token = localStorage.getItem("token");
      if (token && token !== "") {
        config.headers["token"] = token;
      }
    } catch {
      console.log("error put token");
    }
    return config;
  },
  (error) => {
    return Promise.reject(error.response);
  }
);
// Ë∑ØÁî±ÂìçÂ∫îÊã¶Êà™
axios.interceptors.response.use(
  (response) => {
    if (response.data.code === 0) {
      alert(response.data.msg);
    } else {
      return response.data;
    }
  },
  (error) => {
    if (error.response) {
      switch (error.response.status) {
        case 400:
          error.message = `ÈîôËØØËØ∑Ê±Ç`;
          break;
        case 401:
          error.message = `Êú™ÊéàÊùÉÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï`;
          break;
      }
    } else if (error.request) {
      console.log(error.request);
    } else {
      console.log(error.message);
    }
    ElMessage({
      showClose: true,
      message: error.message,
      type: "error",
    });
    return Promise.reject(error.response); // ËøîÂõûÊé•Âè£ËøîÂõûÁöÑÈîôËØØ‰ø°ÊÅØ
  }
);
export default axios;
